

import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './vuex'

import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.prototype.showTost = function(text) {
this.$vux.toast.text(text, 'middle')
}
Vue.prototype.hideTost = function() {
this.$vux.toast.hide()
}
Vue.prototype.showLoading = function() {
this.$vux.loading.show({
text: '加载中'
})
}
Vue.prototype.hideLoading = function() {
this.$vux.loading.hide()
}

Vue.prototype.sendurl = 'https://ecom-uat.capitaland.com.cn/index.html'



const eventTing = new Vue({
el: '#app',
router,
store,
components: {
App
},
template: '<App/>'
})
router.beforeEach((to,from,next) => {
console.log(eventTing.sendurl)
console.log('kd100')
console.log(from)
console.log(to)
console.log(location.search)
if(location.search.match('path=kd100')){
if(location.search.match('id=')){
let id = location.search.split('=').reverse()[0]

window.location.replace(eventTing.sendurl+"#/orderDetail?id="+ id)
}
else{

window.location.replace(eventTing.sendurl+"#/myOrder")
}
}
else{
next()
}
})

export default eventTing


