import * as types from '../mutation-types'

const state={
  hotsearch:[],//热搜
  shoppingList:{},//购物车列表
  shoppingCart:{},//购物车小计
}

const mutations={
  [types.hotsearch](state,data){
    state.hotsearch=data;
  },
  [types.shoppingList](state,data){
    state.shoppingList=data;
  },
  [types.shoppingCart](state,data){
    state.shoppingCart=data;
  },
}

export default {
  state,
  mutations
}



