import * as types from "./mutation-types";
import API from "../api/api";

export const login=({commit})=>{
  API.login()
    .then(res=>{
      localStorage.setItem('token',res.data.token)
      localStorage.setItem('refreshToken',res.data.refreshToken)
    })
}

export const getHotSearch=({commit})=>{
  API.getHotSearch('a')
    .then(res=>{
      commit(types.hotsearch,res.data.resultData.resultList)
    })
}
export const getShoppingCart=({commit},_this)=>{
  API.getShoppingCart()
    .then(res=>{
      if(res.data.resultData.retCode==1){
        let data=res.data.resultData.storeCarts;
        for(let store in data){
          let goods=data[store].shoppingCartItems
          _this.$set(data[store],'isStore',true);
          for(let item in goods){//商品添加是否选中标识
            if(goods[item].isChoose=="Y"){
              _this.$set(goods[item],'isGoods',true);
            }else{
              _this.$set(goods[item],'isGoods',false);
              _this.$set(data[store],'isStore',false);
            }
          }
        }
        commit(types.shoppingList,data);
        commit(types.shoppingCart,res.data.resultData.shoppingCart)
        _this.watchAll();
      }
      _this.hideLoading();
    })
}
export const chooseCartItem=({commit},arr)=>{
  API.chooseCartItem(arr)
    .then(res=>{
        console.log(this)
    })
}
export const getShoppingCartTotal=({commit})=>{
  API.getShoppingCartTotal()
    .then(res=>{
        commit(types.shoppingCart,res.data.resultData.shoppingCart)
    })
}



