const mutations ={
  ['setyourlist'](state,data){
    if(data.flag){
      state.yourlist.list.push(...data.list)
    }
    else{
      state.yourlist = data
    }
  },
  ['frWish'](state,data){//
    if(data.flag){
      if(data.s == 'WISH_HAS_ACHIEVE'){
        state.myWishlt.list.push(...data.list)
      }
      else{
        state.frWishlt.list.push(...data.list)
      }
      return
    }
    if(data.s == 'WISH_HAS_ACHIEVE'){
      state.myWishlt = data
      return
    }
    state.frWishlt = data
  },
  ['setOrdetail'](state,data){
    state.orderTel =data
  },
  ['setUser'](state,data){
  state.user = data
  },
  ['setWwc'](state,data){
    state.wwc[data.type] = data.size
  },
  ['setAdrelb'](state,data){
    state.adressLb.flag=false
  if(data.type){
    state.adressLb.defaultAddressId = data.id
  }
  else{
    state.adressLb = data
  }

  },
  ['clearSsq'](state,data){
    state.ssdq ={
      yiji:[{name:'',value:''}],
      erji:[{name:'',value:''}],
      sanji:[{name:'',value:''}]
    }
  },
  ['setssdq'](state,data){
    let array = data.stateList.map( v => {
      return {name:v.name,value:v.id}
    })
  state.ssdq[data.type] = array
  },
  ['setcoupons'](state,data){
    if(data.flag=='G'){
      state.daijinquan.wsy = data.coupons
      state.daijinquan.wsy1 = 1
    }
    else if(data.flag=='U'){
      state.daijinquan.ysy = data.coupons
      state.daijinquan.ysy1 = 1
    }
    else{
      state.daijinquan.ygq = data.coupons
      state.daijinquan.ygq1 = 1
    }
  },
  ['setdjqDtail'](state,data){
    console.log(data)
    state.jqDtail= data
  },
  ['frPt'](state,data){
    if(data.s == 'TOGETHER_RUNING'){//
      state.ptList['ing'].lists.push(...data.lists)
    }
    else if(data.s == 'TOGETHER_DONE'){
      state.ptList['done'].lists.push(...data.lists)
    }
    else if(data.s == 'TOGETHER_FAIL'){
      state.ptList['fall'].lists.push(...data.lists)
    }
    else{
      state.ptList['all'].lists.push(...data.lists)
    }
  },
  ['setPt'](state,data){
    if(data.s == 'TOGETHER_RUNING'){
      state.ptList['ing'] = data
    }
    else if(data.s == 'TOGETHER_DONE'){
      state.ptList['done'] = data
    }
    else if(data.s == 'TOGETHER_FAIL'){
      state.ptList['fall'] = data
    }
    else{
      state.ptList['all'] = data
    }

  }
}
export default {
  state,getters,actions,mutations
}



